@use "../../assets/sass/variables.scss" as *;
@use "../../assets/sass/typography.scss" as *;

.navbar, .title, .navbar__pages, .nav-page, svg{
    transition: all 0.5s ease-in-out;
}
@keyframes sdown{
    0%{
        clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
    }
    50%{
        clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
    }
    100%{
        clip-path: polygon(0 0, 100% 0, 100 100%, 0 100%);
    }
}

.navbar {
    font-family: $modern-content;
    background-color:transparent;
    color: $font-color;
    position: fixed;
    top: 0;
    width: 100vw;
    //box-shadow: 0px -1.25rem 2.5rem rgba(97, 65, 65, 0.07);
    border-bottom: 1px solid var(button-color);
    z-index: 1;
    display: flex;
    justify-content: space-between;
    transition: all 0.8s ease-in-out;
    max-width: 1000px;
}

.title, .site-header{
    font-weight: 200;
}
.title{
    font-family: $modern-content;
    color: $font-color;
    text-align: left;
    letter-spacing: 0.2rem;
    margin-bottom: 0;
    margin-top: 0.1rem;
    h3, span{
        display: none;
    }
    h1{
        margin-top: 0;
    }
}
.navbar__pages {
    margin-top: 0.3rem;
    margin-bottom: 0.5rem;
   position: relative;
   height: fit-content;
   .nav-page{    
       flex: 1;
       display: flex;
       flex-direction: column;
       align-items: flex-end;
       color: $font-color;
       max-width: 70px;
       font-size: 1.4rem;
      
    }
}

a {
    text-decoration: none;
}

input{
    margin-top: 0.5rem;
}

svg{
    margin-top: 0.5rem;
    fill: $font-color;
    color: $font-color;
}

.sticky{
    position: fixed;
    top: 0;
    width: 100%;
}
@media only screen and (max-width: 800px) {
    .title{
        font-size: 2rem;
    }
    .nav-page {
        font-size: 1.3rem;
        height: 3.5rem;
    }
}
@media only screen and (min-width: 800px) {
    .title{
        font-size: 2rem;
    }
    .nav-page{
        height: 5rem;
        font-size: 2rem;
    }
}
.nav--home, .nav--ReactPortfolio, .nav--{
    transition: all 0.8s ease-in-out;
    flex-direction: column;
    justify-content: center; 
    align-items: center;
    .title{
        font-size: 1.2rem;
        text-align: center;
        span{
            display: inline;
        }
        h1{
            margin-top: 2rem;
        }
        h3{
            display: block;
            margin-bottom: 4rem;
        }
    }
    .navbar__pages{
        width: 100%;
        display: flex;
        flex: 1;
        text-align: center;
      justify-content: space-around;
      .nav-page{
        align-items: center;
      }
    }
}
.navbar-container{
    display: flex;
    justify-content: center; 
    align-items: center;
}
.nav--graphics, .nav--projects, .nav--contact{
    //animation: name duration timing-function delay iteration-count direction fill-mode;
    //animation: sdown 2s ease-out 0s 1 forwards;
    .navbar__pages{
        .nav-page{
            /*svg{
                opacity: 0;
                display: none;
            }*/
            &:hover{
                color: $off-background-color;
                cursor: pointer;
                svg{
                    opacity: 0.99;
                    display: block;
                }
            }
        }
    }
}